import React, { Component } from 'react'
import { Link, animateScroll as scroll } from "react-scroll";

export default class NavbarM extends Component {
    
    constructor(props) {
        super(props);
        this.myRef=React.createRef()
    }

    onClickHandler=(e)=>{
        this.myRef.current.classList.toggle("mobile-nav-active");
        
    }

    onClickNav= e => {
        this.myRef.current.classList.toggle("mobile-nav-active");
        console.log(this.myRef.current);
    }

    render() {
        
        return (
            <div className="row" >
            <div className="col-2 " ref={this.myRef}>
                <i className="bx bx-list-ul mobile-nav-toggle d-xl-none" onClick={this.onClickHandler}></i>
                <header id="header" className="d-flex justify-content-center align-items-center" >
                    <nav id="navbar" className=" navbar nav-menu">
                            <ul>
                                <li >
                                    <Link
                                        className="nav-link "
                                        activeClass="active"
                                        to="main"
                                        spy={true}
                                        smooth={true}
                                        onClick= {this.onClickNav}
                                        duration={500}
                                    >
                                    <i className="bx bx-home"></i> <span>Home</span></Link>
                                </li>
                                
                                <li>
                                    <Link
                                            className="nav-link  "
                                           
                                            to="about"
                                            spy={true}
                                            smooth={true}
                                            onClick= {this.onClickNav}
                                            duration={500}
                                    >
                                    <i className="bx bx-user"></i> <span>About</span>
                                    </Link>
                                </li>

                                <li>
                                    <Link
                                            className="nav-link "
                                            to="resume"
                                            spy={true}
                                            smooth={true}
                                            onClick= {this.onClickNav}
                                            duration={500}
                                    >
                                    <i className="bx bx-file-blank"></i> <span>Resume</span>
                                    </Link>
                                </li>

                                <li>
                                    <Link
                                            className="nav-link  "
                                            
                                            to="contactme"
                                            spy={true}
                                            smooth={true}
                                            onClick= {this.onClickNav}
                                            duration={500}
                                    >
                                    <i className="bx bx-envelope"></i> <span>Contact</span>
                                    </Link>
                                </li>
                                
                                
                            </ul>
                    </nav>      
                </header>
            </div>    
        </div>
        )
    }
}
