import React, { Component } from 'react';
import Footer from './Footer'

export default class ContactMe extends Component {
    state = {
        name:'',
        email:'',
        subject:'',
        message:''
    }

    onChangeHandler = (e)=>{
        this.setState({[e.target.name] : e.target.value});
    }

    onSubmit = (e) => {
        
        this.setState({
            name:'',
            email:'',
            subject:'',
            message:''
        });
    }

    render() {
        const {name,email,subject,message}=this.state;
        return (
        <>    
            <div className="row " id="contactme" style={{ width:"100%"}}>
            <div className="col-md-2 col-sm-0"></div>
                <div className="col-md-10 col-sm-12 d-flex justify-content-center contact">
                
                    <div className="container" data-aos="fade-up">
                        
                        <div className="section-title">
                            <h2 className="mt-5">Contact</h2>
                        </div>
                        <div className="row">
                            <div className="col-lg-3 col-xs-12 justify-content-start mb-4 mx-auto">
                                <div class="info">
                                    <div class="address">
                                        <i class="fas fa-map-marker-alt"></i>
                                        <h4>Location:</h4>
                                        <p>New Delhi, India</p>
                                    </div>

                                    <div class="email">
                                        <i class="fa fa-envelope"></i>
                                        <h4>Email:</h4>
                                        <p>ahuja.anmol01@gmail.com</p>
                                    </div>
                                </div>
                            </div>    
                            <div className="col-lg-9 col-xs-12">
                                <form action="https://docs.google.com/forms/u/0/d/e/1FAIpQLSfran8mv0S3TilZL7iHZdp4m2X9ERALBVtGstwIvHXSJ2L0Qw/formResponse" method="post" role="form" onSubmit={this.onSubmit} >
                                    <div className="row">
                                        <div className="col-md-6 form-group">
                                            <input 
                                            type="text"
                                            name="entry.2005620554" className="form-control" placeholder="Your Name" required 
                                            onChange={this.onChangeHandler}
                                            />
                                        </div>
                                        <div className="col-md-6 form-group mt-3 mt-md-0">
                                            <input 
                                            type="email"
                                            className="form-control" 
                                            name="entry.1045781291" 
                                            id="email" 
                                            placeholder="Your Email" required 
                                            onChange={this.onChangeHandler}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group mt-3">
                                        <input type="text" className="form-control" name="entry.18322498" id="subject" placeholder="Subject" required
                                        
                                        onChange={this.onChangeHandler}
                                        />
                                    </div>
                                    <div className="form-group mt-3">
                                        <textarea 
                                        className="form-control" name="entry.839337160" rows="5" placeholder="Message" required
                                        onChange={this.onChangeHandler}
                                        ></textarea>
                                    </div>
                                   
                                    <div className="text-center"><button className="mt-3 mb-3 btn btn-primary rounded" type="submit">Send Message</button></div>
                                    </form>
                            </div>
                        </div>


                    </div>
                    
                </div>
            </div>

            <div className="row">
                <div className="col-12">
                    <Footer/>
                </div>
            </div>
        </>
        )
    }
}

