import React from 'react';
import ReactTypingEffect from 'react-typing-effect';

const HomePage = () => {
    return (
        <div id="main" className="row" style={{height:"100vh", width:"100%"}} data-aos="fade-up" >
            <div className="col-md-2"></div>
            <div className="col-md-10 d-flex align-items-center">
                <div className="container" data-aos="zoom-in" data-aos-delay="100">
                    <h1>Anmol Ahuja</h1>
                    <p>I'm <ReactTypingEffect 
                            speed="200"
                            eraseDelay="1000" 
                            text={[" Developer!"," Freelancer"," Designer"]}
                        />
                    </p>
                    <div className="social-links">
                        <a href="https://twitter.com/AnmolAhuja20" className="twitter"><i className="bx bxl-twitter"></i></a>
                        <a href="https://github.com/anmolahuja-dev"><i className="bx bxl-github"></i></a>
                        <a href="https://www.instagram.com/anmol.359/" className="instagram"><i className="bx bxl-instagram"></i></a>
                        <a href="https://www.linkedin.com/in/anmol-ahuja-4940a71a0/" className="linkedin"><i className="bx bxl-linkedin"></i></a>
                        <a href="mailto:ahuja.anmol01@gmail.com" ><i className="bx bxs-envelope"></i></a>
                    </div>
                </div>
            </div>   
        </div>
    );
}

export default HomePage;
